 través de la gestión de la información, entender y saber qué es lo que los consumidores opinan y piensan de un producto o servicio hace que se implemente aún más en las empresas la investigación de mercado. Para dicha investigación, existen diferentes técnicas utilizadas que permiten conocer a profundidad un tema en general, estas pueden ser cualitativas, que permiten una compresión y profundidad alta de información, pero, no permiten una generalización de los resultados. Y, por otra parte, están las herramientas cuantitativas, las cuales, a diferencia de las anteriores, permiten un análisis de grandes cantidades numéricas o de cifras representativas que logran cuantificar los resultados (Research Integrity, s.f.).")]),_c('p',[_vm._v("Veamos, a continuación, en el siguiente recurso las técnicas más utilizadas:")]),_vm._m(7),_vm._m(8),_c('separador'),_vm._m(9),_c('p',[_vm._v("Esquematizar es una herramienta indispensable que proporciona al estudiante una visión completa y estructurada de un tema específico de estudio, es importante que su estructura sea de forma lógica y jerarquizada que demuestre la integración de cada idea con las demás; logrando así resumir en gran medida un contenido de manera sencilla y clara. Además, cabe resaltar que los esquemas pueden servir como sistemas alternativos para plasmar ideas de manera más visual, ofreciendo un desarrollo de las capacidades de análisis, síntesis, orden lógico y dominio de temas, asimismo, facilitan la memorización y el aprendizaje debido a que a muchas personas les resulta mucho más fácil poder memorizar imágenes, colores, símbolos o ciertos patrones dentro de los esquemas. ")]),_c('p',[_vm._v("Existen diferentes tipos de esquemas que pueden implementarse dependiendo de cómo y qué se quiera expresar; veámoslos:")]),_c('div',{staticClass:"row mb-5"},[_c('div',{staticClass:"col-sm-6 col-lg-3 mb-4 mb-lg-0"},[_c('div',{staticClass:"tarjeta--boton bg98 p-4"},[_vm._m(10),_c('h3',[_vm._v("Esquema de numeración de ideas"